@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat);
@import url(https://fonts.googleapis.com/css?family=Merriweather);
/*
=============== 
Variables
===============
*/

:root {
  /* dark shades of primary color*/
  --clr-primary-1: hsl(205, 86%, 17%);
  --clr-primary-2: hsl(205, 77%, 27%);
  --clr-primary-3: hsl(205, 72%, 37%);
  --clr-primary-4: hsl(205, 63%, 48%);
  /* primary/main color */
  --clr-primary-5: hsl(205, 78%, 60%);
  /* lighter shades of primary color */
  --clr-primary-6: hsl(205, 89%, 70%);
  --clr-primary-7: hsl(205, 90%, 76%);
  --clr-primary-8: hsl(205, 86%, 81%);
  --clr-primary-9: hsl(205, 90%, 88%);
  --clr-primary-10: hsl(205, 100%, 96%);
  /* darkest grey - used for headings */
  --clr-grey-1: hsl(209, 61%, 16%);
  --clr-grey-2: hsl(211, 39%, 23%);
  --clr-grey-3: hsl(209, 34%, 30%);
  --clr-grey-4: hsl(209, 28%, 39%);
  /* grey used for paragraphs */
  --clr-grey-5: hsl(210, 22%, 49%);
  --clr-grey-6: hsl(209, 23%, 60%);
  --clr-grey-7: hsl(211, 27%, 70%);
  --clr-grey-8: hsl(210, 31%, 80%);
  --clr-grey-9: hsl(212, 33%, 89%);
  --clr-grey-10: hsl(210, 36%, 96%);
  --clr-white: #fff;
  --clr-red-dark: hsl(360, 67%, 44%);
  --clr-red-light: hsl(360, 71%, 66%);
  --clr-green-dark: hsl(125, 67%, 44%);
  --clr-green-light: hsl(125, 71%, 66%);
  --clr-black: #222;
  --transition: all 0.3s linear;
  --spacing: 0.1rem;
  --radius: 0.25rem;
  --light-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  --dark-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  --max-width: 1170px;
  --fixed-width: 620px;
  --border: 2px solid black;
  --darkAccent: #224b4b;
  --lightAccent: #2cda9d;
  --backgroundBase: #cfd8dc;
  --header-background: #00ff99;
  --foregroundColor: #01579b;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Lato, sans-serif;
  background-color: hsl(209, 34%, 30%);
  background-color: var(--clr-grey-3);
  background: url(https://i.pinimg.com/originals/86/2d/be/862dbe526ec300896d4614c82afcdcb7.jpg);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

main {
  margin-top: 2vw;
  width: 800px;
  background-color: hsl(212, 33%, 89%);
  background-color: var(--clr-grey-9);
  border: 10px solid salmon;
}

#drum-machine {
  display: grid;
  grid-template-columns: 1fr 1fr;
  -webkit-align-content: center;
          align-content: center;
}

#clickable_pad {
  max-width: 400px;
  margin: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 0.5rem;
  padding: 20px;
  border: 3px solid black;
  border-radius: 20px;
}

#audio {
  display: none;
}

.drum-pad {
  font-size: 2rem;
  font-weight: bolder;
  border-radius: 10px;
  cursor: pointer;
  box-shadow: 3px 3px 3px 3px pink inset, 2px 2px 2px 2px black;
}

.drum-pad:hover {
  color: blue;
}

.drum-pad:active {
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
}

/* Power  */
#power-btn-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 20px;
}
.power-light {
  margin: 20px;
  /* margin: 20px 20px 20px auto; */
  width: 30px;
  height: 30px;
  border-radius: 50%;
  /* border: 10px solid black; */
  background-color: red;

  box-shadow: inset 0px 1px 3px 3px rgba(250, 250, 250, 0.5),
    0px 0px 3px 2px rgba(226, 0, 0, 0.5);
}
.power-on {
  background-color: green;
}

.btn-animate:active {
  -webkit-transform: translateY(5px);
          transform: translateY(5px);
}
#vol-btn {
  font-size: 3rem;
  display: -webkit-flex;
  display: flex;
}

#display {
  width: 300px;
  height: 3rem;
  text-align: center;
  background-color: black;
  margin: 20px auto 20px auto;
  border-radius: 10px;
  box-shadow: 1px 1px 1px 5px grey;
  color: white;
  font-weight: bolder;
  font-size: 1.6rem;
}

#vol-btn {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: darkred;
  max-width: 300px;
  margin: 20px auto 20px auto;
  border-radius: 10px;
  box-shadow: 1px 1px 1px 5px grey;
}

/* Bank */

#bank {
  color: hsl(209, 61%, 16%);
  color: var(--clr-grey-1);
  margin: 30px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
}

#toggle-btn {
  width: 100px;
  height: 40px;
  background-color: black;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  cursor: pointer;
  margin-left: 20px;
  margin-right: 20px;
}

.toggler {
  height: 30px;
  width: 45px;
  margin-left: 2px;
  background-color: blue;
}

#toggler-text {
  background-color: black;
  color: white;
  font-weight: bolder;
  padding: 20px;
}

.bank-two {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}

footer {
  text-align: center;
  text-decoration: underline;
  font-weight: bolder;
}

/* Music */

.music-container {
  display: grid;
  place-items: center;
}

.music-str {
  min-height: 100px;
  width: 500px;
  background-color: black;
  color: white;
  padding: 15px;
  text-align: center;
  border-radius: 20px;
  box-shadow: 1px 1px 1px 5px grey;
}

.btn-container {
  display: grid;
  grid-template-columns: repeat(2, minmax(70px, 200px));
}

.btn {
  text-align: center;
  font-weight: bolder;
  padding: 10px;
  margin: 0 20px 10px 20px;
}

#clearMusic {
  -webkit-align-self: center;
          align-self: center;
  background-color: darkred;
  color: white;
}

#clearMusic:hover {
  background-color: red;
  color: black;
}

#play-btn {
  background-color: darkgreen;
  color: white;
}

#play-btn:hover {
  background-color: green;
  color: black;
}

/* accessible power button */
.accessible-elem {
  clip: rect(1px 1px 1px 1px); /* IE 6/7 */
  -webkit-clip-path: inset(50%);
          clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  width: 1px;
}

.contact-icons {
  background-color: transparent;
  color: black;
  font-size: 50px;
  padding: 5px;
  margin: 0 20px 0 20px;
  cursor: pointer;
}

.contact-icons:hover {
  -webkit-transform: rotate(-10deg);
          transform: rotate(-10deg);
}

.contact-icons:active {
  -webkit-transform: rotate(20deg);
          transform: rotate(20deg);
}

/* Media query */
@media only screen and (max-width: 800px) {
  body {
    touch-action: manipulation;
  }
  main {
    width: 100vw;
    border: none;
    padding: 1vh;
    margin: 0;
    overflow-x: hidden;
  }

  #drum-machine {
    display: grid;
    grid-template-columns: auto;
    justify-items: center;
  }

  #clickable_pad {
    min-width: 300px;
    max-width: 400px;
    margin-bottom: 2px;
    border: 1px solid black;
    box-shadow: 1px 1px 1px 5px grey;
  }

  #bank {
    margin: 10px;
  }

  #toggle-btn {
    margin-left: 20px;
    margin-right: 20px;
  }

  #toggler-text {
    background-color: black;
    color: white;
    font-weight: bolder;
    padding: 20px;
    box-shadow: 1px 1px 4px black;
  }

  .music-str {
    max-width: 80vw;
  }

  .btn-container {
    display: grid;
    padding: 5px;
    grid-template-columns: repeat(2, minmax(auto, 170px));
  }

  #icon-container {
    text-align: center;
    width: 60vw;
  }

  /* MEDIA QUERY ENDS HERE */
}

